/* Gradient text utility */
.grad-text {
  background: linear-gradient(180deg, #fff, rgba(255,255,255,.6));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

/* Hide scrollbars on marquee container if needed */
.no-scrollbar::-webkit-scrollbar { display:none; }
.no-scrollbar { -ms-overflow-style: none; scrollbar-width: none; }

/* Horizon Hero minimal styles */
.hero-container {
  position: relative;
  width: 100%;
  height: 200vh; /* provides scroll distance to see transitions */
  overflow: hidden;
}
.hero-canvas {
  position: fixed;
  inset: 0;
  width: 100vw;
  height: 100vh;
  display: block;
  z-index: 0;
}
.hero-content {
  position: relative;
  z-index: 10;
  min-height: 100vh;
  display: grid;
  place-content: center;
  text-align: center;
  padding: 2rem;
}
.hero-title {
  font-size: clamp(3rem, 8vw, 8rem);
  letter-spacing: 0.1em;
  font-weight: 800;
}
.hero-subtitle { margin-top: 1rem; opacity: 0.85; }
.side-menu {
  position: fixed;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}
.scroll-progress {
  position: fixed;
  bottom: 1.5rem;
  left: 50%;
  transform: translateX(-50%);
  width: min(90vw, 640px);
  z-index: 10;
}
.progress-track { height: 4px; background: rgba(255,255,255,.15); }
.progress-fill { height: 100%; background: white; }
